<template lang="pug">
.comp-car-list
    .item(v-for="item in count" :class="type" @click="jumpToDetail(item)")
        .cover.bg
        .info
            .title 荣威RX5 2018款 2.0T 两驱自动互联网智惠版
            .sub 2018年/100公里
            .bottom
                .cur 6.19万
                .new 新车价
                    span 8.00万
</template>

<script>
export default {
    props: {
        type: {
            type: String,
            default: 'cell' // row or cell
        },
        count: {
            type: Number,
            default: 10
        }
    },
    methods: {
        jumpToDetail () {
            this.$router.push({
                path: '/car/detail'
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.comp-car-list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 0 1rem;
    .item {
        padding-bottom: 1rem;
        .title {
            padding-top: 0.4rem;
            line-height: 1.2rem;
        }
        .sub {
            font-size: 0.8rem;
            color: #999;
            padding-top: 0.4rem;
        }
        .bottom {
            display: flex;
            padding-top: 0.6rem;
            justify-content: space-between;
            .cur {
                color: rgb(240, 120, 57);
            }
            .new {
                color: #999;
                font-size: 0.8rem;
                span {
                    text-decoration: line-through;
                }
            }
        }
    }
    .item.cell {
        width: 11rem;
        &:nth-child(2n) {
            margin-left: 1rem;
        }
        .cover {
            width: 11rem;
            height: 7rem;
            margin: 0 auto;
            box-shadow: 0 0 5px #ccc;
        }
        .title {
            padding-top: 0.4rem;
        }
    }

    .item.row {
        display: flex;
        align-items: flex-start;
        .cover {
            width: 9rem;
            height: 5.73rem;
            margin: 0 auto;
            box-shadow: 0 0 5px #ccc;
            flex-shrink: 0;
            margin-right: 1rem;
        }
    }
}
</style>



